import React from "react";
import { Close } from "grommet-icons";
import { Layer, Box } from "grommet";

const Modal = ({ full, children, onClose }) => {
  return (
    <Layer full={true}>
      <Box 
        alignContent="center" 
        justify="center"
        direction="row"
      >
      <Box
        height={{ min: '100%'}}
        width="80%"
        overflow="scroll"
        pad="medium"
        justify="center"
        alignContent="center"
        
      >
        <Close
          style={{ cursor: "pointer" }}
          color="plain"
          onClick={() => onClose && onClose()}
        />
        {children}
      </Box>
      </Box>
    </Layer>
  );
};

export default Modal;
